export default {
	data() {
		return {
			input:'',//用户输入运单号
			inputoff:false,
			paging:0,//控制代收发货页面
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [
				{
					name: '全部'
				},
				{
					name: '未发货'
				},
				{
					name: '已发货'
				},
				{
					name: '交易完成'
				},
				{
					name: '异常关闭'
				}
			],
			menuLists: [],//加载订单
			userlist: '' //用户缓存
		};
	},
	computed: {
		formatPrice() {
			return function(val) {
				return val ? parseFloat(val).toFixed(2) : '';
			}
		},
		formatTime() { //格式化时间，并截取到天数
			return function(val) {
				return this.utils.formatdate(val / 1000).substring(0, 10) //substring（）截取时间
			}
		}
	},
	onLoad() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		this.judgeOrder();
	},
	methods: {
		// 更新用户信息
		updateUser: function(data) {
			// 判断是否登录
			this.utils.showloading();
			this.http
				.getApi('/user/login', { storeLogin: 1, storeOpendid: data }, 'post', '')
				.then(res => {
					uni.setStorageSync('userlist', res.data);
					this.judgeOrder();
					this.utils.success('刷新成功！',()=>{
						uni.hideLoading();
					});
				})
				.catch(err => {
				});
		},
		// 确认收货
		Confirm(e){
			// let e = e;
			this.utils.showloading();
			this.http.getApi('/Companyorder/takeOver', {myId:this.userlist.id, orderNo: e }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					this.updateUser(this.userlist.storeOpendid);
				});
				
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
			});
		},
		onKeyInput: function(event) {
		    this.input = event.target.value
			console.log(this.input);
		},
		
		Orderdetails(item){
			console.log(item);
			this.doUrl('pages/index/orderDetails',item);
		},
		
		delivery(){
			this.inputoff = true;
			// let e = e;
			uni.showModal({
			    title: '请填写快递运单号',
			    content: '请在输入框处填写快递运单号',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		delivery2(e){
			this.utils.showloading();
			this.http.getApi('/order/delivery', {orderNo:e, wuliuNo: this.input }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
				this.utils.success('该订单已操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			});
		},
		// 获取全部进订单
		judgeOrder() {
			this.utils.showloading();
				this.http.getApi('/Companyorder/getmyorder', { myId: this.userlist.id }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					this.menuLists = res.data;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			
		},

		// swichMenu: async function(current) {
		// 	//点击其中一个选项
		// 	if (this.currentTab == current) {
		// 		return false;
		// 	} else {
		// 		if (current == 0) {
		// 			this.currentTab = current;
		// 			this.judgeOrder();
		// 		} else {
		// 			this.currentTab = current;
		// 			this.abnormalOrder(current);
		// 		}
		// 	}
		// },
		// purchase(){
		// 	this.paging = 0;
		// 	this.swichMenu();
		// },
		// Shipment(){
		// 	this.paging = 1;
		// 	this.swichMenu();
		// }
	}
};
